/* =====================================================================================================================
// Skin details */

.irs {
	height: 55px;
}
.irs-with-grid {
	height: 75px;
}
.irs-line {
	height: 10px;
	top: 33px;
	background: #eee;
	background: linear-gradient(to bottom, #ddd -50%, $white 150%);
	/* W3C */
	border: 1px solid #ccc;
	border-radius: 16px;
	-moz-border-radius: 16px;
}
.irs-line-left {
	height: 8px;
}
.irs-line-mid {
	height: 8px;
}
.irs-line-right {
	height: 8px;
}
.irs-bar {
	height: 10px;
	top: 33px;
	border-top: 1px solid #428bca;
	border-bottom: 1px solid #428bca;
	background: #428bca;
	background: linear-gradient(to top, rgba(66, 139, 202, 1) 0%, rgba(127, 195, 232, 1) 100%);
	/* W3C */
}
.irs-bar-edge {
	height: 10px;
	top: 33px;
	width: 14px;
	border: 1px solid #428bca;
	border-right: 0;
	background: #428bca;
	background: linear-gradient(to top, rgba(66, 139, 202, 1) 0%, rgba(127, 195, 232, 1) 100%);
	/* W3C */
	border-radius: 16px 0 0 16px;
	-moz-border-radius: 16px 0 0 16px;
}
.irs-shadow {
	height: 2px;
	top: 38px;
	background: $black;
	opacity: 0.3;
	border-radius: 5px;
	-moz-border-radius: 5px;
}
.lt-ie9 .irs-shadow {
	filter: alpha(opacity=30);
}
.irs-slider {
	top: 25px;
	width: 27px;
	height: 27px;
	border: 1px solid #aaa;
	background: #ddd;
	background: linear-gradient(
		to bottom,
		rgba(255, 255, 255, 1) 0%,
		rgba(220, 220, 220, 1) 20%,
		rgba(255, 255, 255, 1) 100%
	);
	/* W3C */
	border-radius: 27px;
	-moz-border-radius: 27px;
	box-shadow: 1px 1px 3px $black-3;
	cursor: pointer;
}
.irs-slider.state_hover,
.irs-slider:hover {
	background: $white;
}
.irs-min,
.irs-max {
	color: #333;
	font-size: 12px;
	line-height: 1.333;
	text-shadow: none;
	top: 0;
	padding: 1px 5px;
	background: $black-1;
	border-radius: 3px;
	-moz-border-radius: 3px;
}
.lt-ie9 .irs-min,
.lt-ie9 .irs-max {
	background: #ccc;
}
.irs-from,
.irs-to,
.irs-single {
	color: $white;
	font-size: 14px;
	line-height: 1.333;
	text-shadow: none;
	padding: 1px 5px;
	background: #428bca;
	border-radius: 3px;
	-moz-border-radius: 3px;
}
.lt-ie9 .irs-from,
.lt-ie9 .irs-to,
.lt-ie9 .irs-single {
	background: #999;
}
.irs-grid {
	height: 27px;
}
.irs-grid-pol {
	opacity: 0.5;
	background: #428bca;
}
.irs-grid-pol.small {
	background: #999;
}
.irs-grid-text {
	bottom: 5px;
	color: #99a4ac;
} 